.footer-copyright {
  color: #fff;
  background-color: #000;
}

.footer-copyright a {
  font-size: 2rem;
  color: #fff;
  text-decoration: none;
}

.footer-copyright a:hover, .footer-copyright a:active, .footer-copyright a:focus {
  color: #fff;
  text-decoration: none;
}

@media screen and (max-width: 574.8px) {
  .footer-copyright a {
    font-size: 1.25rem;
  }
