.intro-wrapper {
  position: relative;
  padding: 20px 0;
  margin: 0;
  background-color: transparent;
}

.intro-wrapper::after {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
  content: "";
  background-image: url("../img/jumbotron-bg.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  opacity: .35;
}

.intro-wrapper .intro {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  padding: 15px;
}

.intro-wrapper .intro .profile-name {
  flex: 2 0 0;
  margin-right: 5px;
}

.intro-wrapper .intro .profile-picture {
  flex: 1 0 0;
  text-align: right;
}

.intro-wrapper .intro .profile-picture img {
  padding: 4px;
  text-align: right;
  border-color: #000;
  border-style: dotted;
  border-width: 2px;
  border-radius: 1rem;
}

@media screen and (max-width: 574.8px) {
  .intro-wrapper {
    padding: 0;
  }
}

@media screen and (min-width: 992px) {
  .skills-tab .fas,
  .skills-tab .fab,
  .skills-tab .far,
  .skills-tab .fal {
    font-size: 2rem;
  }
}

.tag-cloud span {
  display: inline-block;
  padding: 10px 15px;
  margin-right: 5px;
  margin-bottom: 5px;
  font-weight: 400;
  line-height: 1;
  text-transform: uppercase;
  background-color: #fff;
  border: 1px solid #d1d1d1;
  border-radius: 2px;
  transition: all .15s linear 0s;
}

.contact-form .bigicon {
  font-size: 1.25rem;
  color: #dfdfdf;
}

.contact-form .input-icon {
  position: absolute;
  top: calc(50% - .5em);
  left: 1rem;
}

.contact-form input,
.contact-form textarea {
  padding-left: 48px;
}

.contact-form .input-wrapper {
  position: relative;
}

@media screen and (max-width: 766.8px) {
  .contact-row .col-md-6:not(:last-child) {
    margin-bottom: 1rem;
  }
}

.map-container {
  position: relative;
}

.map {
  position: relative;
  height: 0;
  padding-top: 50px;
  padding-bottom: 22.25%;
  overflow: hidden;
}

/* stylelint-disable declaration-no-important  */
.g-recaptcha > .grecaptcha-badge {
  width: 0 !important;
}

.modal-open .contact-modal {
  display: flex !important;
  align-items: center !important;
}

.modal-open .contact-modal .modal-dialog {
  flex-grow: 1;
}

.modal-open .contact-modal .close {
  float: right;
  font-size: 1.3125rem;
  font-weight: 500;
  line-height: 1;
  color: #ff9000;
  opacity: .2;
}

.modal-open .contact-modal .close:hover, .modal-open .contact-modal .close:focus {
  color: #006fff;
  text-decoration: none;
  cursor: pointer;
  opacity: .5;
